/* Затемнение фона */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный цвет */
    display: flex;
    justify-content: center;
    align-items: center; /* Центрирование формы по вертикали */
    animation: fadeIn 0.3s ease-in-out;
    z-index: 1000; /* Чтобы быть выше остальных элементов */
}

/* Стилизованный контейнер формы */
.alertSlide {
    position: relative;
    background-color: #333;
    color: #fff;
    padding: 16px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
    height: 258px;
    width: 208px;
    max-width: 400px;
    text-align: center;
    animation: slideDown 0.3s ease-in-out;
}

/* Анимация для появления фона */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Анимация слайда сверху вниз */
@keyframes slideDown {
    from {
        transform: translateY(-100%);
    }
    to {
        transform: translateY(0);
    }
}

/* Кастомизация кнопки */
.customTextButton {
    color: white !important;
    background-color: #696969 !important;
}

.customTextButton:hover {
    background-color: rgba(255, 255, 255, 0.1) !important;
    color: #ffffff !important;
}

.customTextButton:active {
    background-color: rgba(175, 175, 175, 0.2) !important;
    color: #ffffff !important;
}

