
.dayList {
    width: 80%;
    height: 76%;
    background-color: #646363 !important;
    position: absolute!important;
    color: #ffffff !important;
    left: 50%!important;  /* Center horizontally */
    top: 50%!important;   /* Center vertically */
    transform: translate(-50%, -50%)!important; /* Translate to properly center */
    z-index: 1000 !important;  /* Ensure it appears above other content */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add some shadow for better visibility */
    padding: 20px; /* Add padding for better layout */
    border-radius: 10px; /* Optional: For a better design look */
    display: grid;
}

.items{
}


.customTextButton {
    color: white !important; /* Цвет текста по умолчанию */
    background-color: rgba(255, 255, 255, 0.1)!important; /* Прозрачный фон по умолчанию */
}

.customTextButton:hover {
    background-color: rgba(255, 255, 255, 0.18) !important; /* Легкий прозрачный фон при наведении */
    color: #ffffff !important; /* Цвет текста при наведении */
}

.customTextButton:active {
    background-color: rgba(175, 175, 175, 0.2) !important; /* Цвет фона при клике */
    color: #ffffff !important; /* Цвет текста при клике */
}
