.preClock {
    width: 291px;
    height: 279px;
    text-align: center;
    display: flex;
    align-items: center;
    border: solid 1px #262626;
    border-radius: 60%;
    margin: 5px;
    background: #262626;
    align-content: center;
    justify-content: center;
}
.clock {
    width: 249px;
    height: 239px;
    text-align: center;
    display: flex;
    align-items: center;
    border: solid 1px #262626;
    border-radius: 60%;
    margin: 5px;
    background: #424242;
    align-content: center;
    justify-content: center;
}
.clock .digital-clock {
    padding: 10px;
}
.clock .analog-clock {
    width: 124px;
    height: 120px;
    border: solid 1px #424242;
    border-radius: 50%;
    position: relative;
    background: #2D2D2D;
}
.clock .analog-clock .dial {
    position: absolute;
    left: 50%;
    width: 50px;
    height: 50px;
    transform-origin: bottom left;
}
.clock .analog-clock .dial.seconds {
    border-left: solid 2px #FFFFFF;
}
.clock .analog-clock .dial.minutes {
    border-left: solid 2px #FFFFFF;
}
.clock .analog-clock .dial.hours {
    width: 35px;
    height: 35px;
    top: 15px;
    border-left: solid 4px #FFFFFF;
}

.clock .analog-clock .tick-mark0deg {
    position: absolute;
    left: 50%;
    top: -44%;
    width: 4px;
    height: 15px;
    background-color: #FFFFFF;
    transform-origin: bottom;
}

.clock .analog-clock .tick-mark90deg {
    position: absolute;
    right: -34%;
    top: 37%;
    width: 4px;
    height: 15px;
    background-color: #FFFFFF;
    transform-origin: bottom;
}

.clock .analog-clock .tick-mark180deg {
    position: absolute;
    left: 50%;
    bottom: -34%;
    width: 4px;
    height: 15px;
    background-color: #FFFFFF;
    transform-origin: bottom;
}

.clock .analog-clock .tick-mark270deg {
    position: absolute;
    left: -34%;
    top: 37%;
    width: 4px;
    height: 15px;
    background-color: #FFFFFF;
    transform-origin: bottom;
}

@media (max-width: 914px) {
.preClock{
    width: 262px;
    height: 255px;
}
    .clock{
        width: 238px;
        height: 232px;
    }

}
