.alarmBody {
    background-color: #1E1E1E;
    text-decoration: none;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.alarmTitle {
    font-weight: 100;
    font-style: normal;
    font-size: 100px;
    color: #969595;
    margin-bottom: 20px;
}

.listElement {
    width: 388px;
    height: 70px;
    background-color: #333333;
    border-radius: 20px;
    margin-bottom: 10px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.cardContent {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.timeCard {
    font-size: 24px;
    color: #fff;
    display: flex;
    flex-direction: column;
}

.dimmedText {
    color: rgba(255, 255, 255, 0.5);
}

.activeText {
    color: white;
}

.timeCard span {

}

.addAlarmButtonBlock{
    position: fixed; /* Закрепляем кнопку */
    bottom: 80px;    /* Отступ снизу над навигационной панелью */
    left: 50%;
    z-index: 100;   /*Приоритет*/
    transform: translateX(-50%);
}

.alarmsList {
    max-height: calc(100vh - 400px);
    padding-top: 20px;
    overflow: auto;
}
.customAddAlarmButton{
    height: 34px!important;
    width: 34px!important;
    cursor: pointer!important;
    color: #1E1E1E!important;
}

@media (max-width: 428px){
    .listElement{
        width: 328px;
        height: 50px;
    }
    .timeCard{
        font-size: 22px;
    }
    .alarmTitle{
        font-size: 84px;
    }
}