.timerWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.linkButtons {
    display: flex;
    justify-content: space-between;
    min-width: 80%;
}

.backArrow {
    color: #9b9b9b;
}


.timerTitle {
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;

    font-size: 130px;
    color: #969595;
}

.mainTimerCircle {
    justify-content: center;
    align-items: center;
    width: 398px;
    height: 385px;
    text-align: center;
    border: solid 1px #262626;
    border-radius: 60%;
    background: #262626;
    align-content: center;
}

.secondTimerCircle {
    width: 322px;
    height: 314px;
    border: solid 1px #262626;
    border-radius: 60%;
    background: #424242;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}


.timerInner {
    display: flex;
    justify-content: center;
    gap: 25px;

}

.timerSegment {
    display: flex;
    justify-content: center; /* Центрирует цифры горизонтально */
    align-items: center; /* Центрирует цифры вертикально */
    width: auto; /* Автоматический расчет ширины в зависимости от содержимого */
    padding: 0 10px; /* Добавляет немного отступов для увеличения пространства */
    font-size: 60px;
}

.time {
    color: white;
    width: 68px;
}

.label {
    color: white;
}

.select {
    color: white;
}

.css-1jy569b-MuiFormLabel-root-MuiInputLabel-root.Mui-focused {
    color: #FFFFFF !important;
}

.timerBody {
    background-color: #1E1E1E;
    text-decoration: none;
    min-height: 95vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.inputs {
    margin-top: 20px;
}

.buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 295px;
    margin-top: 20px;
}

.startTimerButton {
    height: 87px;
    width: 85px;
    background-color: rgba(49, 134, 35, 0.25);
    color: rgba(30, 239, 0, 0.74);
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.startTimerButton:disabled {
    background-color: rgba(20, 51, 16, 0.25);
    color: rgba(23, 155, 3, 0.74);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.startTimerButton:hover {
    opacity: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.stopTimerButton {
    height: 87px;
    width: 85px;
    background-color: rgba(194, 84, 60, 0.25);
    color: rgba(245, 61, 20, 0.84);
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.stopTimerButton:hover {
    opacity: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}


.resetTimerButton {
    height: 87px;
    width: 85px;
    background-color: rgba(0, 0, 0, 0.25);
    color: #fff;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.resetTimerButton:disabled {
    background-color: rgba(0, 0, 0, 0.25);
    color: #757474;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 4px;
}

.formControl {
    margin: 8px !important;
    min-width: 120px !important;
    color: white !important;
}

:global(.MuiSelect-select) {
    color: white !important;
}

:global(.MuiOutlinedInput-notchedOutline) {
    border: 2px solid #424242 !important;
}

/* Hover state for input outline */
:global(.MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline) {
    border-color: #bbbbbb !important;
}

/* Focused state for input outline */
:global(.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline) {
    border-color: #9b9b9b !important;
}

:global(.MuiInputLabel-root) {
    color: #fff !important;
}

/* Focused state for input label */
:global(.MuiInputLabel-root.Mui-focused) {
    color: #9b9b9b !important;
}

:global(.MuiSvgIcon-root) {
    color: #9b9b9b !important;
}

:global(.MuiMenuItem-root) {
    color: #1f1f1f !important;
}

:global(.MuiMenuItem-root:hover) {
    background-color: rgba(255, 255, 255, 0.1) !important;
}

:global(.MuiMenuItem-root.Mui-selected) {
    background-color: rgba(255, 255, 255, 0.2) !important;
}

@media (max-width: 552px) {
    .timerTitle {
        font-size: 80px;
    }

    .buttons {
        gap: 100px;
    }

    .mainTimerCircle {
        width: 336px;
        height: 330px;

    }

    .secondTimerCircle {
        width: 268px;
        height: 258px;
    }
    .timerInner{
        gap:10px;
    }
    .timerSegment {
        font-size: 46px;
    }

    .formControl {
        min-width: 77px !important;
    }

    .timerBody {
        min-height: 91vh;
        padding-bottom: 100px;
    }
}

@media (max-width: 914px) {
    .linkButtons {
        min-width: 100%;
    }

    .buttons {
        margin-top: 20px;
    }

}

@media (max-height: 740px){
    .timerTitle{
        font-size: 76px;
    }
    .mainTimerCircle{
        width: 272px;
        height: 268px;
    }
    .secondTimerCircle{
        width: 228px;
        height: 225px;
    }
    .timerInner{
        gap: 0;
    }
    .timerSegment{
        font-size: 40px;
    }
    .inputs{
        margin-top: 20px;
    }
    .buttons{
        margin-top: 20px;
    }
    .startTimerButton{
        height: 76px;
        width: 76px;
        font-size: 19px;
    }
    .resetTimerButton{
        height: 76px;
        width: 76px;
        font-size: 19px;
    }
    .stopTimerButton{
        height: 76px;
        width: 76px;
        font-size: 19px;
    }
}