.dialogContainer {

}

.dialogHeader {
    display: flex;
    justify-content: center;
    padding-top: 6px;
}

.dialogTitle {
    color: white;
}

.inputs {
    display: flex;
    justify-content: center;
    gap: 20px;
    padding-top: 10px;
}
.functionsContainer{
    display: flex;
    align-items: center;
    flex-direction: column;

}
.functionsBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #414141;
    border-radius: 14px;
    max-width: 90%;
    padding: 10px;
}

.repeatBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.repeatBlockWithParameters{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}

.melodyBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}

.labelBlock {
    display: flex;
    align-items: center;
    gap: 155px;
    margin-right: 18px;
}
.line{
    border: 1px solid rgb(103, 102, 102);
    width: 100%;
    margin-top: 4px;

}
.funcTitle {
    color: #ffffff;
}

.rightSideContent{
    display: flex;
}

.description{
  color:  #9b9b9b ;
}

.inputStyle{
    max-width: 41px;
    border: 1px solid transparent;
    background-color: transparent;
    color: #9b9b9b ;
}

.descriptionForWeekDays{
    font-size: 12px;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 3px;
    color:  #9b9b9b ;
}

.dialogContainerSmall {
    background-color: #333333 !important;
    color: white;
    transition: all 0.3s ease;

}

.dialogContainerLarge {
    background-color: #333333 !important;
    color: white;
    transition: all 0.3s ease;
    height: 500px; /* Make it full height when the list is open */
    position: relative; /* Ensure it holds the absolutely positioned list */

}

:global(.MuiDialog-paper) {
    background-color: #333333 !important; /* Задаем нужный цвет фона */
    color: white; /* Цвет текста внутри диалога */
}

.formControl {
    margin: 8px !important;
    min-width: 120px !important;
    color: white !important;
}

:global(.MuiSelect-select) {
    color: white !important;
}

:global(.MuiOutlinedInput-notchedOutline) {
    border: 2px solid #424242 !important;
}

/* Hover state for input outline */
:global(.MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline) {
    border-color: #bbbbbb !important;
}

/* Focused state for input outline */
:global(.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline) {
    border-color: #9b9b9b !important;
}

:global(.MuiInputLabel-root) {
    color: #fff !important;
}

/* Focused state for input label */
:global(.MuiInputLabel-root.Mui-focused) {
    color: #9b9b9b !important;
}

:global(.MuiSvgIcon-root) {
    color: #9b9b9b !important;
}

:global(.MuiMenuItem-root) {
    color: #1f1f1f !important;
}

:global(.MuiMenuItem-root:hover) {
    background-color: rgba(255, 255, 255, 0.1) !important;
}

:global(.MuiMenuItem-root.Mui-selected) {
    background-color: rgba(255, 255, 255, 0.2) !important;
}

.buttonsBlock {
    display: flex;
    margin-top: 10px;
    gap: 20px;
}

.customTextButton {
    color: white !important; /* Цвет текста по умолчанию */
    background-color: transparent !important; /* Прозрачный фон по умолчанию */
}

.customTextButton:hover {
    background-color: rgba(255, 255, 255, 0.1) !important; /* Легкий прозрачный фон при наведении */
    color: #ffffff !important; /* Цвет текста при наведении */
}

.customTextButton:active {
    background-color: rgba(175, 175, 175, 0.2) !important; /* Цвет фона при клике */
    color: #ffffff !important; /* Цвет текста при клике */
}


