/* navBar.module.css */
.container {
    display: flex;
    align-content: center;
    justify-content: center;
    background-color: #333333;
    height: 80px;
    position: fixed; /* Фиксируем элемент */
    bottom: 0; /* Прикрепляем к нижней части экрана */
    width: 100%; /* Делаем на всю ширину экрана */
    z-index: 1000; /* Поверх остальных элементов */
}

.nav {
    display: flex;
    gap: 100px;
    align-items: center;
}

.item {
    display: flex;
    border-radius: 100%;
    width: 67px;
    height: 67px;
    background-color: #B3B3B3;
    justify-content: center;
    align-items: center;
    position: relative; /* Позиционирование для псевдоэлемента ::after */
    transition: border-radius 0.3s ease, background-color 0.3s ease;
}

.item img{
    height: 44px;
    width: 44px;
}

.item::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -6px; /* Подчеркивание под элементом */
    transform: translateX(-50%);
    width: 0;
    background-color: transparent;
    transition: width 0.3s ease, background-color 0.3s ease;
    height: 8px;
    border-radius: 14px;
}

.item:hover {
    border-radius: 14px;
    background-color: #e1e1e1;
}

.activeItem {
    background-color: transparent;
    border-radius: 14px;
}


.item:hover::after, .activeItem::after {
    width: 70%; /* Длина подчеркивания */
    background-color: white; /* Цвет подчеркивания */
}
.activeItem:hover {
    background-color: transparent;
}

@media (max-width: 580px) {
    .nav {
        gap: 44px;
    }

    .item {
        height: 60px;
        width: 60px;
    }
    .item::after{
        bottom: -13px;
        height: 8px;
        border-radius: 14px;
    }
}

@media (max-height: 740px) {
    .nav {
        gap: 34px;
    }
    .item::after{
        bottom: -13px;
        height: 8px;
        border-radius: 14px;
    }
}
