
.clockContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 440px;
    height: 427px;
    text-align: center;
    border: solid 1px #262626;
    border-radius: 60%;
    margin: 5px;
    background: #262626;
    align-content: center;
}

.numbersContainer {
    width: 375px;
    height: 363px;
    text-align: center;
    display: flex;
    align-items: center;
    border: solid 1px #262626;
    border-radius: 60%;
    margin: 5px;
    background: #424242;
    align-content: center;
    justify-content: center;
}

.linkButtons {
    display: flex;
    justify-content: flex-end;
    min-width: 80%;
}

.settingArrow {
    color: #9b9b9b;
}

.stopWatchTitle {
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    margin-bottom: 59px;
    font-size: 130px;
    color: #969595;
}

.clockElements {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 60px;
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    width: 44px;
}

.clockSymbol {
    width: 56px;
    color: #fff;
    font-size: 60px;
}

.clockSymbols {
    color: #fff;
    font-size: 60px;
    font-optical-sizing: auto;
    font-variation-settings: "slnt" 0;
    font-weight: 100;
    width: 30px;

}

.numbersBlock {
    display: flex;
    gap: 4px;
    flex-wrap: nowrap;
}

.body {
    background-color: #1E1E1E;
    text-decoration: none;
    min-height: 95vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 100px;
}

.buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 295px;
    margin-top: 75px;
}

.startButton {
    height: 87px;
    width: 85px;
    background-color: rgba(49, 134, 35, 0.25);
    color: rgba(30, 239, 0, 0.74);
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.startButton:hover {
    opacity: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.stopButton {
    height: 87px;
    width: 85px;
    background-color: rgba(194, 84, 60, 0.25);
    color: rgba(245, 61, 20, 0.84);
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.lapButton {
    height: 87px;
    width: 85px;
    background-color: rgba(0, 0, 0, 0.25);
    color: #fff;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.stopButton:hover {
    opacity: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}


.resetButton {
    height: 87px;
    width: 85px;
    background-color: rgba(0, 0, 0, 0.25);
    color: #fff;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.resetButton:disabled {
    height: 87px;
    width: 85px;
    background-color: rgba(0, 0, 0, 0.25);
    color: #757474;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.lapsContainer {

}

.lapsContainer li {
    color: #9b9b9b;
    font-size: 20px;
}

.lapsH {
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    margin-bottom: 59px;
    font-size: 30px;
    color: #969595;
}

.lapLine {
    border: 1px solid #696969;
}

@media (max-width: 914px) {
    .linkButtons {
        min-width: 100%;
    }

    .stopWatchTitle {
        margin-bottom: 20px;
    }

    .buttons {
        margin-top: 20px;
    }
}

@media (max-width: 552px) {
    .clockContainer {
        width: 366px;
        height: 359px;
    }

    .numbersContainer {
        width: 315px;
        height: 300px;
    }

    .clockElements {

    }

    .clockSymbols {

    }

    .clockSymbol {

    }
}

@media (max-width: 573px) {
    .stopWatchTitle {
        font-size: 70px;
    }

    .buttons {
        gap: 100px;
    }

    .body {
        min-height: 91vh;
        padding-bottom: 100px;
    }
}

@media (max-height: 740px) {
    .stopWatchTitle {
        font-size: 60px;
    }

    .clockContainer {
        width: 272px;
        height: 268px;
    }

    .numbersContainer {
        width: 228px;
        height: 225px;
    }

    .clockElements {
        font-size: 46px;
    }

    .clockSymbol {
        font-size: 46px;
    }

    .startButton {
        height: 76px;
        width: 76px;
        font-size: 19px;
    }

    .stopButton {
        height: 76px;
        width: 76px;
        font-size: 19px;
    }

    .resetButton {
        height: 76px;
        width: 76px;
        font-size: 19px;
    }
    .resetButton:disabled {
        height: 76px;
        width: 76px;
        font-size: 19px;
    }

    .lapButton {
        height: 76px;
        width: 76px;
        font-size: 19px;
    }

}