.clockContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 49px;
}

.settingArrow {
    color: #9b9b9b;
}
.linkButtons{
    display: flex;
    justify-content: flex-end;
    min-width: 80%;
}
.clockTitle {
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    margin-bottom: 59px;
    font-size: 130px;
    color: #969595;
}

.timeBox {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 198px;
    width: 163px;
    background-color: #525252;
    color: #fff;
    font-size: 120px;
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
}

.DefaultClockBlock {
    display: flex;
    gap: 20px;
}

.DefaultClockAndDateContainer {
    display: flex;
    flex-direction: column;
}

.body {
    background-color: #1E1E1E;
    text-decoration: none;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.dateTimeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    text-align: center;
    color: white;
    font-size: 24px;
}



@media (max-width: 914px) {
    .linkButtons{
        min-width: 100%;
    }
    .clockContainer{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .timeBox {
        height: 100px;
        width: 68px;
        font-size: 58px;
    }
    .clockTitle{
        font-size: 100px;
        margin-bottom: 0;
    }
    .body{
        padding-bottom:100px;
    }

}
@media(max-width: 552px){
    .body{
        min-height: 91vh;
        padding-bottom: 100px;
    }
}
@media(max-height:740px){
    .clockTitle{
        font-size:90px
    }
}
